<template>
  <div class="punchin-progress-wrap">
    <div class="top-box">
      <div class="title">心路历程</div>
      <p class="desc">每个自然月中，满足累计打卡天数时，即可获得对应奖品。<br>奖品有限，先到先得哟～</p>
    </div>
    
    <div class="main-content-box">
      <div class="time-tab">
        <ProgressTab @change="changeMonth"></ProgressTab>
      </div>
      <div class="cont-slider-box" ref="sliderBox">
        <div class="mian-content">
        <div class="day-item" 
             :class="['day-item-'+(index+1), {active: count === (index+1)} ]"
             v-for="(item, index) in listData"
             :key="index">
             <!-- <div class="goods-box punchin-card">
               <img src="./imgs/icon-supply.png" class="goods-img">
            </div> -->
          <template v-if="!item.type">
            <span class="day-text" :class="{active: count === (index+1)}">{{item.text}}天</span>
            <img v-if="count === (index+1)" src="./imgs/icon-location.png" class="location-icon">
          </template>
          <template v-else>
            <router-link tag="div" 
                         v-if="item.type==='punchinCard'"
                         :to="(index+1>count) ? '/punchin-progress' : '/supply-punchin'"
                         class="goods-box punchin-card">
              <img :src="item.imgUrl"  
                   class="goods-img"
                   :class="{grayscale: index+1>count}">
            </router-link>
            <a class="goods-box a" :href="(index+1>count) ? 'javascript:;': item.href" v-else>
              <img :src="item.imgUrl"  
                   class="goods-img"
                   @click="clickCard(index+1<=count)"
                   :class="{grayscale: index+1>count}">
            </a>
          </template>
        </div>  
      
      </div>
      </div>
    </div>
  </div>
</template>

<script>
import {clog, findCourse } from "../../api/punchin";
import { parseTime, debounceFn } from "../../utils/tools";
import { baseURL } from "../../utils/config";
import { Toast, Indicator } from "mint-ui";
import ProgressTab  from "./components/ProgressTab";
export default {
  name: 'punchin-progress',
  components: {ProgressTab},
  data() {
    return {
      listData: [],
      count: 0,
      locationIcon: require('./imgs/icon-location.png'),
    }
  },
  methods: {
    getCourseData(data) {
      data = data || {punchinTime: parseTime(new Date(), '{yyyy}-{mm}-{dd}')}
      return findCourse(data)
        .then(res => {
          Indicator.close()
          this.$refs.sliderBox.scrollTop = 0
          if(res.result.businessCode === '0') {
            return res.reply
          } else {
            Toast({
              message: '无记录',
              position: 'center',
              duration: 1000
            });
          }
        })
        .catch(e => {
          Indicator.close()
        }) 
    },
    setData(data) { //detailUrl
      const listData = []
      for (let i = 0; i < 28; i++) {
        const item = {
          text: i + 1,
          type: '',  // 补签卡, 优惠券
          href: ''
        }
        listData.push(item)
      }
      (data.permonth || []).map(item => { //prizType//
        const index = item.prizeDays-1
        const type = item.prizType === 'S00001' ? 'punchinCard' : 'goodsCard'
        listData[index].type = type
        listData[index].imgUrl = baseURL.imgURL + item.prizImg
        listData[index].href = item.detailUrl
      })

      this.listData = listData
      this.count = data.result
    },
    changeMonth: debounceFn(function (monthData) {
      const data = {punchinTime: monthData.endDate}
      this.getCourseData(data)
      .then(data => {
        this.setData(data)
      }) 
    },200, () => {Indicator.open('加载中')}),

    clickCard(flag) {
      console.log(flag);
      if(!flag) return
      console.log(flag);
      
      try {
        clog("早安打卡", [
          "vjf-h5-log",
          "transTime",
          "openid",
          "好物好生活平",
          location.href.split("?")[0],
          "点击优惠券",
          "心路历程页点击优惠券"
        ]);
        _hmt.push(["_trackEvent", "click", '心路历程页', "点击领取优惠券"]);
      } catch(e) {
        console.log('埋点失败' + e);        
      }
    }
  },
}
</script>

