<template>
  <div class="time-range-change">
        <div class="btn" @click="changeRange('prev')">
          <img src="../imgs/arrow-left-white.png" class="arrow prev"/>
        </div>
        <span class="time-range">{{text}}</span>
        <div class="btn" @click="changeRange('next')">
          <img src="../imgs/arrow-right-white.png" class="arrow next"/>
        </div>   
      </div>
</template>

<script>
const DAYRANGE = 60 * 60 * 24 * 1000
export default {
  name: 'ProgressTab',
  props: {
    value: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  data() {
    return {
      diff: null,   // 距离当前的差
      timeRange: {},
      text: ''
    }
  },
  filters: {
    formatDate(val) {
      return val.replace(/\-/g, '.')
    }
  },
  methods: {
    changeRange(flag) {
      if(flag === 'prev') {
        this.diff--
      } else {
        this.diff >= 0 ? this.diff : this.diff++
      }
    },
    getDateStr(timestamp) {
      const targetTime = new Date(timestamp)
      return `${targetTime.getFullYear()}-${this.add0(targetTime.getMonth()+1)}-${this.add0(targetTime.getDate())}`
    },
    add0(val) {
      return val < 10 ? '0' + val : val
    },
    getMonthTimeRange() {
      const day1 = new Date().setDate(1) // 将标准时间设置到当月1号, 31号时非31日月份setMonth会出问题
      const targetDate = new Date(day1).setMonth(new Date().getMonth() + this.diff)
      const nextMonth = new Date(day1).setMonth(new Date().getMonth() + (this.diff + 1))
      
      const startDate = this.getDateStr(new Date(targetDate).setDate(1))
      const endDate = this.getDateStr(new Date(nextMonth).setDate(0))
      return {startDate, endDate}
    },
    setText() {
      console.log('text');
      const date = new Date(this.timeRange.startDate)
      const year = date.getFullYear()
      const month = date.getMonth() + 1
      return `${year}年${month}月累计打卡天数`
    }
  },
  watch: {
    diff(newVal, oldNew) {
      if(newVal!== oldNew) {
        let timeRange = {}
      console.log('changeMonth', this.diff);

      timeRange = this.getMonthTimeRange()
      // this.$emit('input', timeRange)
      this.$emit('change', timeRange)
      this.timeRange = timeRange
      this.text = this.setText()
      }
    }
  },
  created() {
    this.diff = 0;
  }
}
</script>

<style lang="scss" scoped>
.time-range-change {
    height: 50px;
    width: 530px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 32px;
    color: #fff;
    .btn {
      height: 100%;
      width: 70px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .arrow {
      height: 44px;
      width: 44px;
      border-radius: 50%;
    }
  }
</style>